import { useCallback, useEffect, useRef, useState } from 'react';
import plan23 from '../plan_23.json';

function decode(code) {
  let bits = "";
  for (let i = 0; i < code.length; i++) {
    bits += parseInt(code[i], 16).toString(2).padStart(4, 0);
  }

  return plan23.materias.filter((materia, idx) => bits[idx] === "1").map(materia => materia.nombre);
}

export function useCanje(key, initialValue) {
  // State to store our value
  // Pass initial state function to useState so logic is only executed once
  const [storedValue, setStoredValue] = useState(() => {
    if (typeof window === "undefined") {
      return initialValue;
    }
    try {
      const shareCode = new URLSearchParams(document.location.search).get(key);
      if (shareCode !== null) {
        const values = decode(shareCode);
        return values;
      }
      // Get from local storage by key
      const item = window.localStorage.getItem(key);
      // Parse stored json or if none return initialValue
      return item ? JSON.parse(item) : initialValue;
    } catch (error) {
      // If error also return initialValue
      console.log(error);
      return initialValue;
    }
  });

  const setValueRef = useRef();

  // Return a wrapped version of useState's setter function that ...
  // ... persists the new value to localStorage.
  useEffect(() => {
    setValueRef.current = (value) => {
      try {
        // Allow value to be a function so we have same API as useState
        const valueToStore =
        value instanceof Function ? value(storedValue) : value;

        // Save state
        setStoredValue(value);

        // Save to local storage
        if (typeof window !== "undefined") {
          window.localStorage.setItem(key, JSON.stringify(valueToStore));
        }
      } catch (error) {
        // A more advanced implementation would handle the error case
        console.log(error);
      }
    }
  }, [key, storedValue]);

  const setValue = useCallback((value) => {
    setValueRef.current(value)
  }, []);

  return [storedValue ??[], setValue];
}
